.h1 {
    font-size: 10px + 3.5vw !important;
}
#footer {
    position: fixed;
    left: 0;
    bottom: 7px;
    width: 100%;
}
.board {
    min-width: 350px !important;
}
.boardrow {
    height: 5vw;
    min-height: 80px !important;
}
.boardcell {
    z-index: 0;
    cursor: pointer;
}
.boardcell:hover {
    background-color: #efefef;
}
.winningcell {
    background-color: #f7b5b5 !important;
}
.winningcell:hover {
    background-color: #e6a1a1 !important;
}
.draggable {
    cursor: pointer;
}
.oneblock {
    height: 5vw;
    min-height: 80px;
}
.draggable-oneblock {
    height: 3vw;
    min-height: 50px;
    width: 3vw;
    min-width: 50px;
    z-index: 1;
}
.draggable-oneblock:hover {
    background-color: #408f40 !important;
}
.twoblockH {
    height: 5vw;
    min-height: 80px;
}
.draggable-twoblockH {
    height: 3vw;
    min-height: 50px;
    width: 6vw;
    min-width: 100px;
    z-index: 1;
}
.draggable-twoblockH:hover {
    background-color: #f0ad4e !important;
}
.twoblockV {
    height: 10vw;
    min-height: 160px;
}
.draggable-twoblockV {
    height: 6vw;
    min-height: 100px;
    width: 3vw;
    min-width: 50px;
    z-index: 1;
}
.draggable-twoblockV:hover {
    background-color: #0275d8 !important;
}
.fourblock {
    height: 10vw;
    min-height: 160px;
}
.draggable-fourblock {
    height: 6vw;
    min-height: 100px;
    width: 6vw;
    min-width: 100px;
    z-index: 1;
}
.draggable-fourblock:hover {
    background-color: #cf4a46 !important;
}